body {
  font-family: sans-serif;
  padding: 0;
  margin: 0;
}

#app {
  display: grid;
  grid-template-rows: 100px auto;
  height: 100vh;
}

#main-scrollable {
  border: 1px solid red;
  display: grid;
  grid-template-rows: auto 100px;
  overflow: auto;
}

#app header {
  background-color: red;
}

#app main {
  background-color: blue;
  color: white;
}

#app footer {
  background-color: green;
  color: white;
}

#app header,
#app main,
#app footer {
  padding: 2rem;
}
